import React from 'react';
import katex from 'katex';
import ReactHtmlParser from 'react-html-parser';

class MathInline extends React.Component {
    render() {
        let { isInline, text } = this.props;
        console.log(text)
        return(
            isInline? <div style={{display: "inline"}}>{ReactHtmlParser(katex.renderToString(text, {throwOnError: false}))}</div>
            : <div style={{textAlign: "center"}}>{ReactHtmlParser(katex.renderToString(text, {throwOnError: false}))}</div>
        )
    }
}


export default MathInline